// ** Auth Endpoints
export default {
  loginEndpoint: "http://127.0.0.1:8000/api/profile/login",
  registerEndpoint: "http://127.0.0.1:8000/api/profile/register",
  refreshEndpoint: "http://127.0.0.1:8000/api/profile/token/refresh/",
  logoutEndpoint: "http://127.0.0.1:8000/api/profile/logout",

  // ** This will be prefixed in authorization header with token
  // ? e.g. Authorization: Bearer <token>
  tokenType: "Bearer",

  // ** Value of this property will be used as key to store JWT token in storage
  storageTokenKeyName: "accessToken",
  storageRefreshTokenKeyName: "refreshToken",
};
